@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@300;500&display=swap");

*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: "Roboto", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #a2a7b3;
  background-color: #282c34;
}
.App {
  margin: 0 auto;
  padding: 2rem;
  width: 70vw;
  display: flex;
  flex-direction: column;
  row-gap: 5rem;
}
.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App-header {
  width: 100%;
  border-bottom: 1px solid #474c55;
}
.App-header h4 {
  font-weight: 300;
  padding: 1rem 0;
}

.project__wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 12rem;
  height: 12rem;
  background-color: #373c46;
}
.showcase__wraper {
  display: flex;
  flex-wrap: wrap;
  gap: 0.2rem;
}
